import { useState } from "react";
import Palautus from './Palautus';

function Poista() {

    const [textCont, changeTextCont] = useState("Poistetut tiedot tulevat näkyviin tänne")
    const [runCont, changeRunCont] = useState("")

    const handleChange = (e) => {
        e.preventDefault();
        const name = e.target.name;
        const value = e.target.value;
        changeRunCont(values => ({...values, [name]: value}));
    }

    const handleSubmit = (e) => {
        e.preventDefault();

        const osoite = `http://localhost:3030/urheilijat/${runCont.id}`
        fetch(osoite, {
            method:"DELETE"
        })
        .then(res => changeTextCont("Tietojen poisto onnistui!"))
        .catch(error => {
            changeTextCont("Tietojen poisto epäonnistui");
            console.log("ERROR IN DELETING")
        })
    }

    return (
        <div className="centered">
            <h2>POISTA URHEILIJAN TIEDOT</h2>
            <p>Syötä poistettavan urheilijan ID-numero ja paina "poista". Voit etsiä urheilijan ID:n "Lue" valikosta.</p>
            <form onSubmit={handleSubmit}>
                <input type="number" name="id" placeholder="42" onChange={handleChange} required/>
                <input type="submit" value="Poista" />
            </form>

            <Palautus content={textCont} />
        </div>
    )
}

export default Poista;