import { useState } from "react";
import Palautus from "./Palautus";

function Paivita() {

    const [syoteData, muutaSyote] = useState("")
    const [textCont, changeTextCont] = useState("Päivitetyt tiedot ilmestyvät tänne.")

    const handleChange = (e) => {
        e.preventDefault()
        const key = e.target.name;
        const value = e.target.value;
        muutaSyote(values => ({...values, [key] : value}))
    }

    const handleSubmit = (e) => {
        e.preventDefault()

        const osoite = 'http://localhost:3030/urheilijat'
        fetch(osoite, {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(syoteData)
        })
        .then( res => changeTextCont("Tietojen päivitys onnistui!"))
        .catch( error => {
            changeTextCont("Tietojen päivitys epäonnistui. Tarkista syöte ja yritä uudellen.")
            console.log("ERROR IN UPDATING")
        })
    }

    return (
        <div className="centered">
            <h2>PÄIVITÄ URHEILIJAN TIEDOT</h2>
            <p>Syötä haluamasi urheilijan ID-numero ja täytä muutettavat tiedot. Urheilijan ID:n voit etsiä käyttämällä "Lue"-valikkoa.</p>
            <form onSubmit={handleSubmit}>
                <table>
                    <tr> <td> <b>Urheilijan ID</b> </td> <td> <input name="id" type="number" placeholder="42" onChange={handleChange} required/> </td> </tr>
                    <br />
                    <tr> <td>Paino</td> <td><input name="paino" type="number" placeholder="60" onChange={handleChange} required/></td> </tr>
                    <tr> <td>Saavutukset</td> <td><input name="saavutukset" type="text" placeholder="Olympian-kultaa x2" onChange={handleChange} required/></td> </tr>
                    <tr> <td>Kuva</td> <td><input name="kuva" type="url" placeholder="http://..." onChange={handleChange} required/></td>
                        <td> <input type="submit" value="Päivitä" /> </td> </tr>
                </table>
            </form>
            
            <Palautus content={textCont} />
        </div>
    )
}

export default Paivita;