import Navigaatio from './myComponents/Navigaatio.js'
import Lue from './myComponents/Lue.js'
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Lisaa from './myComponents/Lisaa.js';
import Paivita from './myComponents/Paivita.js';
import Poista from './myComponents/Poista.js';

function App() {
  return (
    <div className="App">
      <Router>
        <Navigaatio />
        <div className="content">
          <Switch>
            <Route exact path="/">
              <Lue />
            </Route>
            <Route path="/lue">
              <Lue />
            </Route>
            <Route path="/lisaa">
              <Lisaa />
            </Route>
            <Route path="/paivita">
              <Paivita />
            </Route>
            <Route path="/poista">
              <Poista />
            </Route>
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default App;
