import { useState } from "react";
import Palautus from "./Palautus";

function Lue() {

    const [nimi, uusiNimi] = useState("")
    const [teksti, uusiTeksti] = useState("Haun tulos ilmestyy tähän.")

    const keepItUp = (e) => {
        e.preventDefault();

        const osoite = `http://localhost:3030/urheilijat/${nimi.nimi}/${nimi.sukunimi}/${nimi.kutsumanimi}/${nimi.syntymavuosi}/${nimi.laji}/${nimi.paino}`

        fetch(osoite)
        .then(res => {
            return res.json()
        })
        .then(data => uusiTeksti(JSON.stringify(data)))
        .catch(error => console.log("ERROR"))
    }

    const haeKaikki = (e) => {
        e.preventDefault();

        const osoite = "http://localhost:3030/urheilijat/kaikki"
        fetch(osoite).then(res => {
            return res.json()
        })
        .then(data => uusiTeksti(JSON.stringify(data)))
        .catch(error => {
            uusiTeksti("Jokin meni pieleen. Tarkista yhteys API:hin");
            console.log("ERROR IN FETCHING ALL");
        })
    }

    const handleChange = (e) => {
        e.preventDefault();
        const name = e.target.name;
        const value = e.target.value;
        uusiNimi(values => ({...values, [name]: value}))
    }

    return (
        <div className="centered">
            <h2>HAE URHEILIJA</h2>
            <p>Täytä tarvitsemasi kentät ja paina "hae". Kaikkia kenttiä ei tarvitse täyttää.</p>
            <form onSubmit={keepItUp}>
                <table>
                    <tr> <td>Nimi </td> 
                        <td> <input type="text" name="nimi" placeholder="Seppo" onChange={handleChange}/> </td> </tr>
                    <tr> <td>Sukunimi </td> 
                        <td> <input type="text" name="sukunimi" placeholder="Mäkäräinen" onChange={handleChange}/> </td> </tr>
                    <tr> <td>Kutsumanimi </td> 
                        <td> <input type="text" name="kutsumanimi" placeholder="Peppi" onChange={handleChange}/> </td> </tr>
                    <tr> <td>Syntymävuosi </td> 
                        <td> <input type="text" name="syntymavuosi" placeholder="1980" onChange={handleChange}/> </td> </tr>
                    <tr> <td>Laji </td> 
                        <td> <input type="text" name="laji" placeholder="Eukonkanto" onChange={handleChange}/> </td> </tr>
                    <tr> <td>Paino </td> 
                        <td> <input type="text" name="paino" placeholder="60" onChange={handleChange}/> </td>
                        <td> <input type="submit" value="Hae"/> </td> 
                        <td> <input type="button" value="Hae Kaikki" onClick={haeKaikki} /> </td> </tr>
                </table>
            </form>

            <Palautus content={teksti} />
        </div>
    );
}

export default Lue;