import { useState } from "react";
import Palautus from "./Palautus";

function Lisaa() {

    const [textCont, changeTextCont] = useState("Lisätyn urheilijan tiedot ilmestyvät tähän.")
    const [runCont, changeRunCont] = useState("");

    const handleChange = (e) => {
        e.preventDefault();
        const name = e.target.name;
        const value = e.target.value;
        changeRunCont(values => ({...values, [name]: value}))
    }

    const handleSubmit = (e) => {
        e.preventDefault();

        fetch("http://localhost:3030/urheilijat", {
            method: "POST",
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(runCont)
        })
        .then( res => changeTextCont("Tietojen lisääminen onnistui!"))
        .catch( error => {
            changeTextCont("Tietojen lisääminen epäonnistui. Tarkista syöte ja yritä uudelleen.")
            console.log("ERROR IN ADDING");
        })
    }
    
    return (
        <div className="centered">
            <h2>LISÄÄ URHEILIJA</h2>
            <p>Kaikki kentät tulee täyttää. Urheilijan ID-numero määräytyy itsenäisesti.</p>
            <form onSubmit={handleSubmit}>
                <table>
                    <tr> <td>Nimi </td>
                        <td> <input type="text" name="nimi" placeholder="Seppo" onChange={handleChange} required/> </td> </tr>
                    <tr> <td>Sukunimi </td> 
                        <td> <input type="text" name="sukunimi" placeholder="Mäkäräinen" onChange={handleChange} required/> </td> </tr>
                    <tr> <td>Kutsumanimi </td> 
                        <td> <input type="text" name="kutsumanimi" placeholder="Peppi" onChange={handleChange} required/> </td> </tr>
                    <tr> <td>Syntymävuosi </td> 
                        <td> <input type="text" name="syntymavuosi" placeholder="1980" onChange={handleChange} required/> </td> </tr>
                    <tr> <td>Laji </td> 
                        <td> <input type="text" name="laji" placeholder="Eukonkanto" onChange={handleChange} required/> </td> </tr>
                    <tr> <td>Paino </td> 
                        <td> <input type="text" name="paino" placeholder="60" onChange={handleChange} required/> </td> </tr>
                    <tr> <td>Saavutukset</td>
                        <td> <input type="text" name="saavutukset" placeholder="MM-Kultaa" onChange={handleChange} required/> </td></tr>
                    <tr> <td>Kuvalinkki </td>
                        <td> <input type="text" name="kuva" onChange={handleChange} placeholder="http://..." required/> </td>
                        <td> <input type="submit" value="Lisää"/> </td> </tr>
                </table>
            </form>
            
            <Palautus content={textCont} />
        </div>
    )
}

export default Lisaa;